
#content{height:100%;overflow-y:auto;padding:66px 0 50px
}
.task_list_box{padding:10px 0
}
.fl{float:left
}
.fr{float:right
}
.rili{color:#4399d8
}
.red{color:#f00
}
.list-group-item{position:relative
}
.list-group-item span{line-height:30px
}
.list-group-item label{font-weight:normal
}
.list-group-item .remove{position:absolute;right:-5px;top:-4px;line-height:15px
}
.green{color:#1f8214;font-weight:normal
}
#dialog{position:fixed;left:5%;width:90%;z-index:10;max-width:600px;overflow-y:auto;border-radius:4px;background:#fff
}
.box{overflow:hidden
}
.wrap{position:fixed;left:0;top:0;width:100%;height:100%;z-index:9;background:#000;opacity:0.5
}
.task_list{margin-bottom:0
}
input,textarea{width:100%;max-width:154px
}



